<template>
  <div id="home">
    <!-- 网页头部 -->
    <nav-bar class="home-nav">
      <div class slot="center">购物街</div>
    </nav-bar>
    <tab-control
      class="tab-control"
      :title="['流行','新款','精选']"
      @tabClick="tabClick"
      ref="tabControl1"
      v-show="isFixed"
    />
    <!-- 滚动区域 -->
    <scroll
      class="content"
      ref="scroll"
      :probe-type="3"
      :pull-up-load="true"
      @scroll="contentScroll"
      @pullUpLoad="loadMore"
    >
      <!-- 轮播图部分 -->
      <home-swiper :banners="banners" @swiperImageLoad="swiperImageLoad" />
      <!-- 小人头部分 -->
      <recmmend-view :recommends="recommends" />

      <!-- 本周流行部分 -->
      <feature-view />
      <!-- 商品选项切换区域 -->
      <tab-control :title="['流行','新款','精选']" @tabClick="tabClick" ref="tabControl2" />
      <!-- 商品展示部分 -->
      <goods-list :goods="showGoods" />
    </scroll>
    <!-- 回到顶部 -->
    <back-top @click.native="backClick" v-show="isShowBackTop" />
  </div>
</template>
<script>
//home内的子组件
import HomeSwiper from "./childComps/HomeSwiper";
import RecmmendView from "./childComps/RecmmendView";
import FeatureView from "./childComps/FeatureView";
//可复用组件
//多个项目都可使用
import NavBar from "components/common/navBar/NavBar";
import Scroll from "components/common/scroll/Scroll";

//业务逻辑公共部分组件
import TabControl from "components/content/tabControl/TabControl";
import GoodsList from "components/content/goods/GoodsList";
import { itemListenerMixin, backTopMixin } from "common/mixin";

//网络请求组件
import { getHomeMultidata, getHomeData } from "network/home";
import { get } from "http";
import { debounce } from "common/utils";
export default {
  name: "Home",
  components: {
    HomeSwiper,
    RecmmendView,
    FeatureView,
    Scroll,

    NavBar,
    TabControl,
    GoodsList
  },
  mixins: [itemListenerMixin, backTopMixin],
  data() {
    return {
      //轮播图数据
      banners: [],
      //小人头数据
      recommends: [],
      //商品展示数据
      //三大块 page:数据的页码 list储存的数据
      goods: {
        pop: { page: 0, list: [] },
        new: { page: 0, list: [] },
        sell: { page: 0, list: [] }
      },
      //用来储存展示那页商品默认pop
      currentType: "pop",
      //回顶部是否显示 默认不显示
      tabOffsetTop: 0,
      isFixed: false,
      saveY: 0
    };
  },
  computed: {
    showGoods() {
      //展示的数据的改变
      return this.goods[this.currentType].list;
    }
  },
  activated() {
    this.$refs.scroll.scrollTo(0, this.saveY, 0);
    this.refresh();
  },
  deactivated() {
    this.saveY = this.$refs.scroll.getScrollY();
    this.$bus.$off("itemImageLoad", this.itemImgListener);
  },
  created() {
    //请求数据
    //轮播图数据
    this.getHomeMultidata();
    //pop数据
    this.getHomeData("pop");
    //new数据
    this.getHomeData("new");
    //sell数据
    this.getHomeData("sell");
  },
  mounted() {
    //监听item中的图片加载
    //不能再create内直接拿this.$refs
    //防抖操作
  },
  methods: {
    //事件监听
    //切换点击商品类型
    tabClick(index) {
      switch (index) {
        case 0:
          this.currentType = "pop";
          break;
        case 1:
          this.currentType = "new";
          break;
        case 2:
          this.currentType = "sell";
          break;
      }
      this.$refs.tabControl1.currentIndex = index;
      this.$refs.tabControl2.currentIndex = index;
    },

    //监听滚动
    contentScroll(position) {
      //显示回到顶部按钮
      this.isShowBackTop = -position.y >= 1000;
      //决定是否吸顶
      this.isFixed = -position.y > this.tabOffsetTop;
    },
    //下拉加载更多数据
    loadMore() {
      this.getHomeData(this.currentType);
      // console.log(this.$refs.scroll.scroll);
    },
    //监听轮播图图片是否加载完
    swiperImageLoad() {
      // console.log(this.$refs.tabControl2.$el.offsetTop);
      //获取吸顶的距离
      this.tabOffsetTop = this.$refs.tabControl2.$el.offsetTop;
    },

    //网络请求的方法
    //轮播图数据保存
    getHomeMultidata() {
      getHomeMultidata().then(res => {
        this.banners = res.data.banner.list;
        this.recommends = res.data.recommend.list;
      });
    },
    //商品展示数据保存
    getHomeData(type) {
      //动态获取保存数据页码
      const page = this.goods[type].page + 1;
      getHomeData(type, page).then(res => {
        //将新加载的数据加到数组内
        this.goods[type].list.push(...res.data.list);

        this.goods[type].page += 1;
        //调用scroll内置方法让加载更多数据可以再次使用
        this.$refs.scroll.finishPullUp();
      });
    }
  }
};
</script>
<style scoped>
#home {
  /* padding-top: 44px; */
  height: 100vh;
  position: relative;
}
.home-nav {
  /* css动态设置变量 */
  background-color: var(--color-tint);
  color: white;
  /* position: fixed;
  left: 0;
  right: 0;
  top: 0;
  z-index: 9; */
}
.tab-control {
  /* position: sticky; */
  /* top: 44px; */
  position: relative;
  left: 0;
  right: 0;
  z-index: 9;
}
.content {
  overflow: hidden;
  position: absolute;
  top: 44px;
  bottom: 49px;
  left: 0;
  right: 0;
}
/* .content{
  height: calc(100% - 93px);
  overflow: hidden;
} */
</style>